	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>2016.9 – 2017.9</p>\n</blockquote>\n<h5>Rehabilitation + Arts + Game + Performance, as a new approach of art therapy.(deploy)</h5>\n<br>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.9021190716448%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 01\"\n        title=\"\"\n        src=\"/portfolio/static/img-01-db7917220223edd61534579b18e298c0-22850.png\"\n        srcset=\"/portfolio/static/img-01-db7917220223edd61534579b18e298c0-271ac.png 148w,\n/portfolio/static/img-01-db7917220223edd61534579b18e298c0-df984.png 295w,\n/portfolio/static/img-01-db7917220223edd61534579b18e298c0-22850.png 590w,\n/portfolio/static/img-01-db7917220223edd61534579b18e298c0-03843.png 885w,\n/portfolio/static/img-01-db7917220223edd61534579b18e298c0-7c4f9.png 1180w,\n/portfolio/static/img-01-db7917220223edd61534579b18e298c0-4c9ae.png 1770w,\n/portfolio/static/img-01-db7917220223edd61534579b18e298c0-9f2a1.png 1982w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n<em>One of the audience was playing the rehab-game\nduring the exhibition in Taipei. (2016.11)</em></p>\n<h2>Background</h2>\n<blockquote>\n<p>062720150832    484     15     44 %    18-29    ∞</p>\n</blockquote>\n<p>These numbers represent an incident that shocked Taiwanese society—the Formosa Fun Coast explosion.</p>\n<p>The incident happened at 8.32 pm. on June 27, 2015. Total injuries were 484 people, including 15 deaths, with 44 percent burns to their bodies. Most sadly, the victims were young people between the ages of 18 and 29. Many of the burn patients are still undergoing physical and psychological treatment.</p>\n<h2>Healing through Art and Tech</h2>\n<p>Through this project, Aluan Wang and I aim to bring back a colorful future to young victims with music, art and technology. The patients not only have to keep up to the tempo of the game, but also are able to compose their own melodies using the application (music game) created by the digital artists. The application interacts with Kinect technology and engages in the process of rehabilitation. It detects the gesture of the player. If the movement of their body accords with the requirement of their therapy, the program will make a corresponding audio and visual feedback.</p>\n<p>The addition of the music and creativity content in their rehabilitation aimed to bring a more positive atmosphere to them. The whole process was recorded so it could be repeated for the society. </p>\n<p>“Étude” is a French word, meaning the musical composition designed for practicing a certain skill. It’s a metaphor for rehabilitation procedures of the victims and the showcases of the artists.</p>\n<br>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 52.49376558603491%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 02\"\n        title=\"\"\n        src=\"/portfolio/static/img-02-caca4f1204aaa358d80bc0854546a8f4-22850.png\"\n        srcset=\"/portfolio/static/img-02-caca4f1204aaa358d80bc0854546a8f4-271ac.png 148w,\n/portfolio/static/img-02-caca4f1204aaa358d80bc0854546a8f4-df984.png 295w,\n/portfolio/static/img-02-caca4f1204aaa358d80bc0854546a8f4-22850.png 590w,\n/portfolio/static/img-02-caca4f1204aaa358d80bc0854546a8f4-03843.png 885w,\n/portfolio/static/img-02-caca4f1204aaa358d80bc0854546a8f4-7c4f9.png 1180w,\n/portfolio/static/img-02-caca4f1204aaa358d80bc0854546a8f4-4c9ae.png 1770w,\n/portfolio/static/img-02-caca4f1204aaa358d80bc0854546a8f4-7c85c.png 3208w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 47.83018867924528%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 03\"\n        title=\"\"\n        src=\"/portfolio/static/img-03-b0d7914438bc8cc41c30a0c861a0e1c0-22850.png\"\n        srcset=\"/portfolio/static/img-03-b0d7914438bc8cc41c30a0c861a0e1c0-271ac.png 148w,\n/portfolio/static/img-03-b0d7914438bc8cc41c30a0c861a0e1c0-df984.png 295w,\n/portfolio/static/img-03-b0d7914438bc8cc41c30a0c861a0e1c0-22850.png 590w,\n/portfolio/static/img-03-b0d7914438bc8cc41c30a0c861a0e1c0-03843.png 885w,\n/portfolio/static/img-03-b0d7914438bc8cc41c30a0c861a0e1c0-7c4f9.png 1180w,\n/portfolio/static/img-03-b0d7914438bc8cc41c30a0c861a0e1c0-4c9ae.png 1770w,\n/portfolio/static/img-03-b0d7914438bc8cc41c30a0c861a0e1c0-35ab0.png 2120w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 39.252584150543335%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 04\"\n        title=\"\"\n        src=\"/portfolio/static/img-04-c084a1a8a75b39a8c575b04c3f31ec9a-22850.png\"\n        srcset=\"/portfolio/static/img-04-c084a1a8a75b39a8c575b04c3f31ec9a-271ac.png 148w,\n/portfolio/static/img-04-c084a1a8a75b39a8c575b04c3f31ec9a-df984.png 295w,\n/portfolio/static/img-04-c084a1a8a75b39a8c575b04c3f31ec9a-22850.png 590w,\n/portfolio/static/img-04-c084a1a8a75b39a8c575b04c3f31ec9a-03843.png 885w,\n/portfolio/static/img-04-c084a1a8a75b39a8c575b04c3f31ec9a-7c4f9.png 1180w,\n/portfolio/static/img-04-c084a1a8a75b39a8c575b04c3f31ec9a-4c9ae.png 1770w,\n/portfolio/static/img-04-c084a1a8a75b39a8c575b04c3f31ec9a-eb794.png 3773w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n<em>The patients were playing\nthe game during their rehabilitation. (2016.7)</em></p>\n<br>\n<h2>Showcase</h2>\n<br>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 307px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.74491392801252%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 05\"\n        title=\"\"\n        src=\"/portfolio/static/img-05-7af08229396e919e559cf79677194700-d439b.jpg\"\n        srcset=\"/portfolio/static/img-05-7af08229396e919e559cf79677194700-9bb1d.jpg 148w,\n/portfolio/static/img-05-7af08229396e919e559cf79677194700-4899b.jpg 295w,\n/portfolio/static/img-05-7af08229396e919e559cf79677194700-d439b.jpg 590w,\n/portfolio/static/img-05-7af08229396e919e559cf79677194700-9c164.jpg 885w,\n/portfolio/static/img-05-7af08229396e919e559cf79677194700-a40e7.jpg 1180w,\n/portfolio/static/img-05-7af08229396e919e559cf79677194700-08ae7.jpg 1278w\"\n        sizes=\"(max-width: 307px) 100vw, 307px\"\n      />\n    </span>\n  </span>\n  \n<em>After the performance, we were discussing with audience. (2016.11)</em></p>\n<p>Based on the users’ composing record, I extracted melody patterns and generated music from them by using live coding techniques, known as algorithmic rave. I performed as improvisers, creating live music and using their self-made instruments. The showcase acts as a platform that documents how these music therapy sessions find possibilities of rehabilitation and fine art.</p>\n<br>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.29778672032192%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 06\"\n        title=\"\"\n        src=\"/portfolio/static/img-06-e7dffa63b2ac2850cece28e0b9cc4d72-c35c0.jpg\"\n        srcset=\"/portfolio/static/img-06-e7dffa63b2ac2850cece28e0b9cc4d72-60ae1.jpg 148w,\n/portfolio/static/img-06-e7dffa63b2ac2850cece28e0b9cc4d72-85602.jpg 295w,\n/portfolio/static/img-06-e7dffa63b2ac2850cece28e0b9cc4d72-c35c0.jpg 590w,\n/portfolio/static/img-06-e7dffa63b2ac2850cece28e0b9cc4d72-024fa.jpg 885w,\n/portfolio/static/img-06-e7dffa63b2ac2850cece28e0b9cc4d72-a404e.jpg 1180w,\n/portfolio/static/img-06-e7dffa63b2ac2850cece28e0b9cc4d72-83f30.jpg 1770w,\n/portfolio/static/img-06-e7dffa63b2ac2850cece28e0b9cc4d72-44071.jpg 1988w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.80121089808276%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"img 07\"\n        title=\"\"\n        src=\"/portfolio/static/img-07-376f8b836294bd4401312596a533027c-c35c0.jpg\"\n        srcset=\"/portfolio/static/img-07-376f8b836294bd4401312596a533027c-60ae1.jpg 148w,\n/portfolio/static/img-07-376f8b836294bd4401312596a533027c-85602.jpg 295w,\n/portfolio/static/img-07-376f8b836294bd4401312596a533027c-c35c0.jpg 590w,\n/portfolio/static/img-07-376f8b836294bd4401312596a533027c-024fa.jpg 885w,\n/portfolio/static/img-07-376f8b836294bd4401312596a533027c-a404e.jpg 1180w,\n/portfolio/static/img-07-376f8b836294bd4401312596a533027c-83f30.jpg 1770w,\n/portfolio/static/img-07-376f8b836294bd4401312596a533027c-28ebf.jpg 1982w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n<em>Étude Taipei Showcase. (2016.11)</em></p>\n<br>\n<h2>Cooperation and Teamwork</h2>\n<p>This project was advised by Ministry of Culture of the Republic of China, sponsored by Quanta Computer. Hence, I had to not only design the system of data extraction and performance, but also coordinate with different artists and organizations.</p>\n<p>I am the invited artist of this project, in charge of the design of the music game for procedures of rehabilitation, and the program composing the data of their recordings, in addition to the performance of the showcase.</p>\n<br>\n<h2>Links</h2>\n<p><a href=\"https://www.aec.at/ai/en/etude/\">> Ars Electronica Festival 2017</a><br>\n<a href=\"http://www.clickfestival.dk/etude\">> Click Festival 2017</a><br>\n<a href=\"https://www.youtube.com/watch?v=zU01cFDjL6s\">> Showcase in Taipei 2016</a><br>\n<a href=\"https://www.youtube.com/watch?v=jnp93Dy3REY\">> Interview with The Patients</a><br>\n<a href=\"https://github.com/vibertthio/etude\">> source code</a>  </p>","frontmatter":{"title":"Étude"}}},"pathContext":{"slug":"/projects/2016/01/etude/","_PARENT":"SOURCE"}}

